.App {
  font-family: 'Roboto', sans-serif;
  text-align: center;
  padding: 20px;
}

.title {
  font-size: 36px;
  margin-bottom: 20px;
}

.status {
  font-size: 24px;
  margin-bottom: 20px;
}

.board {
  display: grid;
  grid-template-columns: repeat(3, 100px);
  grid-template-rows: repeat(3, 100px);
  gap: 10px;
  margin-bottom: 20px;
  justify-content: center;
}

.square {
  font-size: 60px;
  font-weight: bold;
  border: none;
  background-color: #f7f7f7;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
}

.square:hover {
  background-color: #e0e0e0;
}

.square:focus {
  outline: none;
  box-shadow: 0 0 0 3px rgba(0, 0, 0, 0.2);
}

.reset {
  font-size: 24px;
  font-weight: bold;
  border: none;
  background-color: #4CAF50;
  color: white;
  padding: 10px 20px;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
}

.reset:hover {
  background-color: #3e8e41;
}

.reset:focus {
  outline: none;
  box-shadow: 0 0 0 3px rgba(0, 0, 0, 0.2);
}
.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.popup-content {
  background-color: white;
  padding: 20px;
  border-radius: 10px;
  text-align: center;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  animation: scaleUp 0.3s ease-in-out;
}

@keyframes scaleUp {
  from {
    transform: scale(0.8);
    opacity: 0.5;
  }

  to {
    transform: scale(1);
    opacity: 1;
  }
}

.celebration {
  font-size: 36px;
  margin: 20px 0;
}

.new-game {
  font-size: 18px;
  font-weight: bold;
  border: none;
  background-color: #007bff;
  color: white;
  padding: 10px 20px;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
  border-radius: 5px;
}

.new-game:hover {
  background-color: #0056b3;
}

.new-game:focus {
  outline: none;
  box-shadow: 0 0 0 3px rgba(0, 0, 0, 0.2);
}